import { HOME } from "config";
import { useEffect, useState } from "react";
import { Link } from "react-router-dom";

export default function CheckoutPage() {
    const [phone, setPhone] = useState('');
    const setPhoneNu = (e) => {
        setPhone(e);
    }
    useEffect(() => {
        const urlParams = new URLSearchParams(window.location.search);
        setPhoneNu(urlParams.get('phone'));
    });


    return (
        <main style={{ minHeight: 'calc(100vh - 43px - 264px)' }}>
            <section>
                <div className="container align--center">
                    <div className="gap--50"></div>
                    <div className="gap--50"></div>
                    <div className="gap--50"></div>
                    <h3>Đơn hàng đã được đặt thành công!</h3>
                    <h4 id="phone">{phone}</h4>
                    <h5>Nhân viên cửa hàng sẽ sớm liên lạc với bạn để xác nhận đơn hàng cũng như tư vấn các ưu đãi khác!</h5>
                    <div className="gap--50"></div>
                    <div className="gap--50"></div>
                    <Link className="btn black pi--50" to={HOME + '/'}>Trở về trang chủ</Link>
                </div>
            </section>
        </main>
    )
}